body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form.searchBar input[type=text] {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 50%;
  background: #f1f1f1;

}

/* Style the submit button */
form.searchBar button {
  float: left;
  width: 10%;
  padding: 10px;
  background: #2196F3;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none; /* Prevent double borders */
  cursor: pointer;
}

form.searchBar button:hover {
  background: #0b7dda;
}

/* Clear floats */
form.searchBar::after {
  content: "";
  clear: both;
  display: table;
}

form.searchBar {
  margin-top: 20%;
  margin-left: 30%;
}

form.sign-up-form{
  border: 1px solid grey;
  width: 50%;
  background: #f1f1f1;
  padding: 10px;
}


div.Card{
  float: left;
  width: 50%;
  margin-top: 20px;
  margin-left: 2%;
  border: 1px solid;
  padding: 10px;
  box-shadow: 1px 1px;
  border-radius: 15px 50px 30px 5px; ;
}

div.newsCard{
  float: right;
  width: 30%;
  margin-top: 20px;
  border: 2px solid;
  padding: 10px;
  margin-right: 2%;
  box-shadow: 1px 1px;
  border-radius: 25px;
}

form.edit-info-form{
  width: 20%;
  margin-left: 20%;
}

div.avatarImg{
  float: right;
  margin-right: 20%;
}

div.game-info{
  width: 70%;
  margin-top: 10%;
  border: 2px solid;
  padding: 10px;
  box-shadow: 1px 1px;
  border-radius: 15px;
}
